* {
  padding: 0;
  margin: 0;
  font-family: "Jost", sans-serif;
}
body {
  background: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 98%;
  width: 99%;
  margin: 7px 0 10px 8px;
  font-family: "Jost", sans-serif;
}

html {
  font-size: 100%;
  height: 100%;
}
html {
  background: #acf5ff;
}
.box {
  width: 30vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(240, 7, 7, 0.2);
  background: #000000;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  min-width: 20rem;
  min-height: 35rem;
  box-shadow: 0 0 15px 2px rgba(96, 255, 52, 0.9);
}
.wave {
  position: absolute;
  top: 150%;
  left: 50%;
  background: rgba(96, 255, 52, 1);
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  opacity: 0.1;
  z-index: 1;
}
.wave.-three {
  animation: drift 7000ms infinite linear;
  opacity: 0.1;
  z-index: 2 !important;
}
.wave.-two {
  animation: drift 7000ms infinite linear;
  opacity: 0.1;
  z-index: 3 !important;
}
.box::after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
}
@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.info {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 60vh;
  z-index: 4;
  color: #acf5ff;
}
.location {
  margin-top: -5.5rem;
  text-align: center;
  font-weight: 800;
  font-size: 3rem;
  text-transform: capitalize;
}
.fa-street-view {
  animation: rotates 3s linear infinite alternate;
}
@keyframes rotates {
  from {
    transform: translateX(-0.5rem);
  }
  to {
    transform: translateX(0.5rem);
  }
}
#date {
  text-align: center;
  margin-top: 0.5rem;
  color: #57606f;
  font-size: 1.2rem;
  font-weight: 300;
  text-transform: uppercase;
}
.location {
  font-family: "Quantico", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: rgb(78, 240, 66);
}
.Tempapp {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 3rem;
}
.tempmin_max {
  text-align: center;
  margin-top: 0.3rem;
  font-weight: 300;
  font-size: 1.2rem;
}
#weathercon {
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3em;
}
#weathercon .fas {
  font-size: 6rem;
  animation: fas-anime 3s linear infinite alternate;
}
html {
  background: #eee;
}
@keyframes fas-anime {
  from {
    transform: scale(1.1);
  }
  to {
    transform: scale(1.5);
  }
}
@media (max-width: 600px) {
  .box {
    height: 80vh;
    width: 90vw;
  }
  .wave {
    top: 85%;
  }
  #weathercon {
    font-size: 5em;
  }
  .info {
    font-size: 1.5rem;
  }
}
@media (max-width: 500px) {
  .box {
    height: 80vh;
  }
  .wave {
    top: 115%;
  }
}
body > span {
  width: 100vw;
  text-align: center;
  color: grey;
  padding: 20px;
}
.inputData {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputField {
  width: 100%;
  margin-top: -5%;
  border-radius: 0.5rem;
  padding: 20px 25px;
  outline: none;
  font-size: 20px;
  border-width: 0px;
  box-shadow: 0 0 14px 2px rgba(96, 255, 52, 0.9);
  color: white;
  background-color: #000000;
}
.errorMsg {
  text-align: center;
  font-size: 2rem;
  margin: 2rem auto;
  font-family: "Quantico", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: rgb(78, 240, 66);
}
